module.exports = "<div class=\"head\">\r\n    <div class=\"container\">\r\n        <h1>Orange B<span class=\"text-primary\">oo</span>sted with Angular</h1>\r\n        <p class=\"lead\">This is Ng-boosted, built with Boosted (css only), Angular and ng-bootstrap.</p>\r\n        <!--<a class=\"btn btn-inverse btn-info btn-lg disabled\" href=\"\">Download Ng-boosted</a>-->\r\n        <p class=\"version\">Currently v{{ngBoostedVersion}}</p>\r\n    </div>\r\n</div>\r\n\r\n<div class=\"container\">\r\n    <h2>Getting started</h2>\r\n\r\n    <h3>Dependencies</h3>\r\n    <p>Ng-boosted is a set of custom components and directives that allows you to run your Angular based projet with Boosted CSS.</p>\r\n    <p>Before your start using Ng-boosted you need the following dependencies :</p>\r\n        <ul class=\"o-square-list\">\r\n            <li><a href=\"http://boosted.orange.com\">Boosted {{boostedVersion}}</a></li>\r\n            <li><a href=\"https://angular.io/\">Angular {{angularVersion}}</a></li>\r\n            <li><a href=\"https://ng-bootstrap.github.io/\">ng-bootstrap {{ngBootstrapVersion}}</a></li>\r\n        </ul>\r\n\r\n    <h3>Installation</h3>\r\n    <p>For <code>ng-bootstrap</code> installation please see the documentation <a href=\"https://ng-bootstrap.github.io/#/getting-started\">here</a></p>\r\n\r\n    <p>After installing the above dependencies, install <code>ng-boosted</code> via:</p>\r\n\r\n    <p><code>npm install --save ng-boosted</code></p>\r\n\r\n    <p>Once installed you need to import <code>ng-bootstrap</code> and <code>ng-boosted</code> modules.</p>\r\n\r\n    <p><code>\r\n        import {{ '{' }} NgbModule {{ '}' }} from '@ng-bootstrap/ng-bootstrap';<br>\r\n        import {{ '{' }} NgBoostedModule {{ '}' }} from 'ng-boosted';\r\n    </code></p>\r\n\r\n    <p><code>\r\n        @NgModule({{ '{' }}<br>\r\n        &emsp;declarations: [AppComponent, ...],<br>\r\n        &emsp;imports: [NgbModule.forRoot(), NgBoostedModule, ...],<br>\r\n        &emsp;bootstrap: [AppComponent]<br>\r\n        {{ '}' }})<br>\r\n        export class AppModule {{ '{' }}<br>\r\n        {{ '}' }}<br>\r\n    </code></p>\r\n\r\n    <h2>Building this documentation</h2>\r\n    <p>Simply <code>npm run build:docs</code> and the output goes to a <code>_gh_pages</code> folder.</p>\r\n\r\n    <p>You can also run this documentation with <code>npm start</code></p>\r\n\r\n    <p>The output is then served to <a href=\"http://localhost:3030\">http://localhost:3030</a>\r\n    </p>\r\n\r\n    <h2>Copyright and license</h2>\r\n\r\n    <p>Code and documentation copyright 2017 Orange SA. Code released under <a href=\"https://github.com/Orange-OpenSource/Orange-Boosted-Angular/blob/master/LICENSE\">the MIT license</a>. Docs released under <a href=\"https://github.com/Orange-OpenSource/Orange-Boosted-Angular/blob/master/docs/LICENSE\">Creative Commons Attribution</a>.\r\n    </p>\r\n</div>\r\n"

